<template>
  <section class="section score">
    <h2 class="section__title">„Çπ„Ç≥„Ç¢</h2>
    <p class="hidden">{{getQuestionAnswer}}</p>
    <p class="hidden">{{getUserAnswer}}</p>
    <div class="score__block">
      <p class="score__result">{{getQuestionLength}}Âïè‰∏≠<span class="score__resultAttention">{{calcScore}}Âïè</span>Ê≠£Ëß£„Åß„Åó„Åü</p>
    </div>
  </section>
</template>

<script>
import $ from 'jquery';

export default {
  name: 'score',
  props: [ // App.vue „Åã„ÇâË≥™Âïè/Ëß£Á≠î„É™„Çπ„Éà„ÅÆÂèó„ÅëÂèñ„Çä
    'qaData',
    'answerData'
  ],
  data () {
    return {
      correctAnswerArray: [],
      userAnswerArray: []
    }
  },
  methods: {
  },
  computed: {
    // Ë®≠ÂïèÊï∞„ÅÆÂèñÂæó
    getQuestionLength: function() {
      const qaDataLength = this.qaData.length; // Ë®≠Âïè„Éá„Éº„ÇøÊï∞
      return qaDataLength;
    },
    // Ë®≠Âïè„ÅÆËß£Á≠î„ÇíÈÖçÂàó„Å´Ê†ºÁ¥ç
    getQuestionAnswer: function() {
      const thisQaData = this.qaData;
      thisQaData.forEach((value) => {
        this.correctAnswerArray.push(value.ansCorrect);
      });
      return this.correctAnswerArray;
    },
    // „É¶„Éº„Ç∂„Éº„ÅÆËß£Á≠î„ÇíÈÖçÂàó„Å´Ê†ºÁ¥ç
    getUserAnswer: function () {
      // „É¶„Éº„Ç∂„Éº„ÅÆËß£Á≠î„Éá„Éº„Çø„ÅÆÂèñÂæóÔºàAnswer.vue„ÅÆ `userAnswer` „Å®Âêå„ÅòÔºâ
      const pickedAnswerArray = this.answerData.pickedAnswer;   // „É©„Ç∏„Ç™„Éú„Çø„É≥„ÅÆËß£Á≠îÂèñÂæó
      const checkedAnswerArray = this.answerData.checkedAnswer; // „ÉÅ„Çß„ÉÉ„ÇØ„Éú„ÉÉ„ÇØ„Çπ„ÅÆËß£Á≠îÂèñÂæó
      // „É©„Ç∏„Ç™„Éú„Çø„É≥/„ÉÅ„Çß„ÉÉ„ÇØ„Éú„ÉÉ„ÇØ„Çπ„ÅÆËß£Á≠îÈÖçÂàó„ÇíÁµêÂêà
      // ref: https://qiita.com/takeharu/items/d75f96f81ff83680013f
      const concatAnswerArray = pickedAnswerArray.concat(checkedAnswerArray);
      // null „ÇíÂâäÈô§
      // ref: https://qiita.com/akameco/items/1636e0448e81e17e3646
      const deletedBlankAnswerArray = concatAnswerArray.filter(Boolean);
      // ÊòáÈ†Ü„ÇΩ„Éº„Éà
      // ref: https://qiita.com/PianoScoreJP/items/f0ff7345229871039672
      const sortAnswerArray = deletedBlankAnswerArray.sort((a,b) => {
        if( a < b ) return -1;
        if( a > b ) return 1;
        return 0;
      });
      // ÂêÑË®≠Âïè„Åî„Å®„ÅÆ„Ç™„Éñ„Ç∏„Çß„ÇØ„Éà„Å´Ê†ºÁ¥ç
      // „Ç™„Éñ„Ç∏„Çß„ÇØ„Éà„ÅÆ„Ç≠„Éº„Å´Â§âÊï∞„Çí‰Ωø„ÅÜ
      // ref: https://qiita.com/kmagai/items/95481a3b9fd97e4616c9
      // „Ç™„Éñ„Ç∏„Çß„ÇØ„Éà„Å∏„ÅÆpush
      // ref: https://stackoverflow.com/questions/8925820/javascript-object-push-function/8925849
      const objectAnswer = [];
      const objectAnswerArray = [];
      sortAnswerArray.forEach((value,key) => {
        const valueString = value.toString();
        const questionIndex = valueString.match(/\d{1,}_/g)[0].replace('_','');  // Ê≠£Ë¶èË°®Áèæ„ÅßË≥™Âïè„ÅÆ„Ç§„É≥„Éá„ÉÉ„ÇØ„Çπ„ÇíÂèñÂæó
        const questionAnswer = valueString.match(/_\D{1,}/g)[0].replace('_',''); // Ê≠£Ë¶èË°®Áèæ„ÅßË≥™Âïè„ÅÆÂõûÁ≠î„ÇíÂèñÂæó
        objectAnswer[key] = {
          id: Number(questionIndex),
          answer: questionAnswer
        }
        objectAnswerArray.push(objectAnswer[key]);
      });
      // Âêå„Åò„Ç≠„ÉºÔºàË≥™ÂïèIDÔºâ„ÇíÊåÅ„Å£„ÅüÁ≠î„Åà„ÅØÈÖçÂàó„Å´„Åæ„Å®„ÇÅ„Çã
      // ref: https://stackoverflow.com/questions/57319602/
      const data = objectAnswerArray.reduce((acc, value) => {
        acc[value.id] = acc[value.id] ? acc[value.id] : [];
        acc[value.id] ? acc[value.id].push(value.answer) : [value.answer];
        return acc;
      }, {});
      let formattedAnswerArray = Object.entries(data).map(d => ({ id: d[0], answer: d[1] }) );
      // „É¶„Éº„Ç∂„Éº„ÅÆËß£Á≠î„ÇíÈÖçÂàó„Å´Ê†ºÁ¥ç
      this.userAnswerArray = []; // ÂàùÊúüÂåñ
      formattedAnswerArray.forEach((value) => {
        this.userAnswerArray.push(value.answer);
      });
      return this.userAnswerArray;
    },
    // „Çπ„Ç≥„Ç¢„ÅÆË®àÁÆó
    calcScore: function () {
      const correctAnswer = this.correctAnswerArray;
      const userAnswer = this.userAnswerArray;
      let userScore = 0;
      correctAnswer.forEach((value,index) => {
        const correctAnswerValue = value;
        const userAnswerValue = userAnswer[index];
        if ( array_equal(correctAnswerValue,userAnswerValue) ) {
          userScore ++;
        }
      });
      return userScore;
    }
  }
}
// ÈÖçÂàó„ÅÆÁ≠â‰æ°ÊØîËºÉ
// ref: https://marycore.jp/prog/js/array-equal/
function array_equal(a, b) {
  if (!Array.isArray(a))    return false;
  if (!Array.isArray(b))    return false;
  if (a.length != b.length) return false;
  for (var i = 0, n = a.length; i < n; ++i) {
    if (a[i] !== b[i]) return false;
  }
  return true;
}
</script>

<style lang="scss" scoped>
.score {
  display: none;
  &.is-active {
    display: block;
  }
}
.hidden {
  display: none;
}
.score__block {
  background: $lightYellow_01;
  border: $lightYellow_02 4px solid;
  margin-top: 20px;
  padding: 40px;
  box-shadow: 0 0 10px rgba($black_01,.1);
  border-radius: 2px;
  @include mq {
    padding: 30px;
  }
}
.score__result {
  @include title01;
  @include fontBold;
  text-align: center;
}
.score__resultAttention {
  color: $red_01;
  font-size: 42px;
  margin: 0 10px;
  @include mq {
    font-size: 36px;
    margin: 0 8px;
  }
}
</style>



