<template>
  <section class="section check">
    {{userAnswerCheck}}
    <div class="check__block">
      <template v-if="this.checkButtonFlag === true">
        <p class="check__text">üëªÁ≠î„ÅàÂêà„Çè„Åõ„Çí„Åó„Å¶„Åø„Åæ„Åó„Çá„ÅÜ üëª</p>
        <button class="check__button" v-on:click="questionAnswerList">Á≠î„ÅàÂêà„Çè„Åõ</button>
      </template>
      <template v-else>
        <p class="check__text">üôÑ„Åô„Åπ„Å¶„ÅÆË≥™Âïè„Å´Ëß£Á≠î„Åó„Å¶„Åè„Å†„Åï„ÅÑ üôÑ</p>
        <button class="check__button" disabled>Á≠î„ÅàÂêà„Çè„Åõ</button>
      </template>
    </div>
  </section>
</template>

<script>
import $ from 'jquery';

export default {
  name: 'check',
  props: [ // App.vue „Åã„ÇâË≥™Âïè/Ëß£Á≠î„É™„Çπ„Éà„ÅÆÂèó„ÅëÂèñ„Çä
    'qaData',
    'answerData'
  ],
  data () {
    return {
      checkButtonFlag: false
    }
  },
  methods: {
    // Á≠î„ÅàÂêà„Çè„Åõ„Éú„Çø„É≥„ÇØ„É™„ÉÉ„ÇØÊôÇ„ÅÆÊåôÂãï
    questionAnswerList: function (event) {
      // Ë®≠Âïè„ÅÆËß£Á≠î„ÅåÂÆå‰∫Ü„Åó„Å¶„ÅÑ„Çã„Å®„Åç
      const $check = $('.check');
      const checkHeight = $('.check').innerHeight();
      const $answer = $('.answer');
      const $score = $('.score');
      // „Çπ„Ç≥„Ç¢„Ç®„É™„Ç¢ / Ëß£Á≠î„Ç®„É™„Ç¢„ÇíË°®Á§∫
      $score.addClass('is-active');
      $answer.addClass('is-active');
      // „Çπ„ÇØ„É≠„Éº„É´ÁßªÂãï
      const targetPosition = $check.offset().top + checkHeight + 10;
      $('html, body').stop().animate({ scrollTop: targetPosition }, 500, 'swing');
    }
  },
  computed: {
    // Ë®≠Âïè„ÅÆËß£Á≠î„ÅåÂÆå‰∫Ü„Åó„Å¶„ÅÑ„Çã„Åã„ÅÆÂà§Êñ≠
    userAnswerCheck: function () {
      // „É¶„Éº„Ç∂„Éº„ÅÆËß£Á≠î„Éá„Éº„Çø„ÅÆÂèñÂæóÔºàAnswer.vue„ÅÆ `userAnswer` „Å®Âêå„ÅòÔºâ
      const pickedAnswerArray = this.answerData.pickedAnswer;   // „É©„Ç∏„Ç™„Éú„Çø„É≥„ÅÆËß£Á≠îÂèñÂæó
      const checkedAnswerArray = this.answerData.checkedAnswer; // „ÉÅ„Çß„ÉÉ„ÇØ„Éú„ÉÉ„ÇØ„Çπ„ÅÆËß£Á≠îÂèñÂæó
      // „É©„Ç∏„Ç™„Éú„Çø„É≥/„ÉÅ„Çß„ÉÉ„ÇØ„Éú„ÉÉ„ÇØ„Çπ„ÅÆËß£Á≠îÈÖçÂàó„ÇíÁµêÂêà
      // ref: https://qiita.com/takeharu/items/d75f96f81ff83680013f
      const concatAnswerArray = pickedAnswerArray.concat(checkedAnswerArray);
      // null „ÇíÂâäÈô§
      // ref: https://qiita.com/akameco/items/1636e0448e81e17e3646
      const deletedBlankAnswerArray = concatAnswerArray.filter(Boolean);
      // ÊòáÈ†Ü„ÇΩ„Éº„Éà
      // ref: https://qiita.com/PianoScoreJP/items/f0ff7345229871039672
      const sortAnswerArray = deletedBlankAnswerArray.sort((a,b) => {
        if( a < b ) return -1;
        if( a > b ) return 1;
        return 0;
      });
      // ÂêÑË®≠Âïè„Åî„Å®„ÅÆ„Ç™„Éñ„Ç∏„Çß„ÇØ„Éà„Å´Ê†ºÁ¥ç
      // „Ç™„Éñ„Ç∏„Çß„ÇØ„Éà„ÅÆ„Ç≠„Éº„Å´Â§âÊï∞„Çí‰Ωø„ÅÜ
      // ref: https://qiita.com/kmagai/items/95481a3b9fd97e4616c9
      // „Ç™„Éñ„Ç∏„Çß„ÇØ„Éà„Å∏„ÅÆpush
      // ref: https://stackoverflow.com/questions/8925820/javascript-object-push-function/8925849
      const objectAnswer = [];
      const objectAnswerArray = [];
      sortAnswerArray.forEach((value,key) => {
        const valueString = value.toString();
        const questionIndex = valueString.match(/\d{1,}_/g)[0].replace('_','');  // Ê≠£Ë¶èË°®Áèæ„ÅßË≥™Âïè„ÅÆ„Ç§„É≥„Éá„ÉÉ„ÇØ„Çπ„ÇíÂèñÂæó
        const questionAnswer = valueString.match(/_\D{1,}/g)[0].replace('_',''); // Ê≠£Ë¶èË°®Áèæ„ÅßË≥™Âïè„ÅÆÂõûÁ≠î„ÇíÂèñÂæó
        objectAnswer[key] = {
          id: Number(questionIndex),
          answer: questionAnswer
        }
        objectAnswerArray.push(objectAnswer[key]);
      });
      // Âêå„Åò„Ç≠„ÉºÔºàË≥™ÂïèIDÔºâ„ÇíÊåÅ„Å£„ÅüÁ≠î„Åà„ÅØÈÖçÂàó„Å´„Åæ„Å®„ÇÅ„Çã
      // ref: https://stackoverflow.com/questions/57319602/
      const data = objectAnswerArray.reduce((acc, value) => {
        acc[value.id] = acc[value.id] ? acc[value.id] : [];
        acc[value.id] ? acc[value.id].push(value.answer) : [value.answer];
        return acc;
      }, {});
      let formattedAnswerArray = Object.entries(data).map(d => ({ id: d[0], answer: d[1] }) );

      // Ëß£Á≠î„ÅÆ„Éá„Éº„ÇøÊï∞„ÄÅË®≠Âïè„Éá„Éº„ÇøÊï∞„ÇíÊØî„Åπ„Å¶Âêå„ÅòÊï∞„Åß„ÅÇ„Çå„Å∞Á≠î„ÅàÂêà„Çè„Åõ„Éú„Çø„É≥„ÇíÊ¥ªÊÄßÂåñ
      const answerDataLength = formattedAnswerArray.length; // Ëß£Á≠î„ÅÆ„Éá„Éº„ÇøÊï∞
      const qaDataLength = this.qaData.length;              // Ë®≠Âïè„Éá„Éº„ÇøÊï∞
      if ( answerDataLength === qaDataLength ) {
        // Á≠î„ÅàÂêà„Çè„Åõ„Éú„Çø„É≥„ÇíÊ¥ªÊÄßÂåñ
        this.checkButtonFlag = true;
      } else {
        // Á≠î„ÅàÂêà„Çè„Åõ„Éú„Çø„É≥„ÅØÈùûÊ¥ªÊÄß
        this.checkButtonFlag = false;
        // „Çπ„Ç≥„Ç¢„Ç®„É™„Ç¢ / Ëß£Á≠î„Ç®„É™„Ç¢„ÇíÈùûË°®Á§∫
        const $answer = $('.answer');
        const $score = $('.score');
        $score.removeClass('is-active');
        $answer.removeClass('is-active');
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.check__block {
}
.check__text {
  @include text02;
  @include fontBold;
  text-align: center;
}
.check__button {
  margin-top: 15px;
  background: none;
  border: 0;
  width: 100%;
  padding: 18px 0;
  background: $red_01;
  box-shadow: 0 0 10px rgba($black_01,.3);
  border: $red_01 4px solid;
  border-radius: 2px;
  color: $white_01;
  font-size: 24px;
  line-height: 1;
  @include fontBold;
  @include anime;
  @include mq {
    margin-top: 10px;
    padding: 18px 0;
    font-size: 20px;
  }
  &:hover {
    cursor: pointer;
    background: $white_01;
    color: $red_01;
    @include mq {
      background: $red_01;
      color: $white_01;
    }
  }
  &[disabled="disabled"] {
    opacity: .4;
    &:hover {
      cursor: default;
      background: $red_01;
      color: $white_01;
    }
  }
}
</style>



